<template>
  <section class="container" :loading="allLoading">
   <div v-if="studentInfo.id!=''">
     <div class="divbuju">
       <el-row :gutter="20">
         <el-col :span="11">欢迎登陆！恭喜您正式成为我院{{studentInfo.c.grade}}级{{studentInfo.sb.major}}专业{{studentInfo.c.name}}的学生
         </el-col>
       </el-row>
       <el-row :gutter="20">
         <el-col :span="6">您的学号：</el-col>
         <el-col :span="3">{{studentInfo.studentId}}</el-col>
       </el-row>
       <el-row :gutter="20">
         <el-col :span="6">您的宿舍：</el-col>
         <el-col :span="3">{{dormitoryInfo.dong}}{{dormitoryInfo.floor}}-{{dormitoryInfo.room}}</el-col>
       </el-row>
     </div>

     <el-row :gutter="20">

       <el-col :span="10">

         <div class="divbuju">
           <el-row>
             <el-col :span="12">班主任：{{headmasterInfo.name}}</el-col>
           </el-row>
           <el-row>
             <el-col><i class="el-icon-mobile-phone"></i>{{headmasterInfo.phone}}</el-col>
           </el-row>
           <el-row>
             <el-col >邮箱：{{headmasterInfo.email}}</el-col>
           </el-row>
         </div>

       </el-col>

       <el-col :span="14" v-if="classnotice != undefined || classnotice.length > 0">
         <div class="divbuju">
           <el-row>
             <el-col :span="4">班级通知：</el-col>

           </el-row>
           <hr/>
           <div v-for="cn in classnotice">
             <el-row>
               <el-col>{{cn.noticeWord}}</el-col>
             </el-row>
             <el-row>
               <el-col :span="12" :offset="12">{{cn.time}}</el-col>
             </el-row>
             <hr/>
           </div>
         </div>
       </el-col>
     </el-row>
   </div>

    <div v-else>
      <h1>第一次登录，正在排队领取学号，宿舍，请稍等10秒！</h1>
    </div>

  </section>
</template>

<script>
  export default {
    name: "Stustudent",
    created() {

    },
    mounted() {
      this.initstudent();
    },
    methods: {
      initstudent() {
        this.allLoading = true;
        var _this = this;
        this.postRequest('/student/status/initStudent').then(resp => {
          // this.studentInfo = resp.data[0];
          // this.dormitoryInfo = resp.data[1];
          // this.headmasterInfo = resp.data[2];
          // this.classnotice=resp.data[3];
          // // this.allLoading=false;
          // // _this.$store.submitStudent(resp.data[0].c,resp.data[1]);
          // console.info(JSON.stringify(resp.data));
          // // if(this.$store.StaStustudentsign){
          // //   location.reload();
          // //   this.$store.commit('updateStaStustudentsign');
          // // }
          this.postRequest('/student/status/initStudent').then(resp => {
            this.studentInfo = resp.data[0];
            this.dormitoryInfo = resp.data[1];
            this.headmasterInfo = resp.data[2];
            this.classnotice=resp.data[3];
            this.allLoading=false;
            // _this.$store.submitStudent(resp.data[0].c,resp.data[1]);
            console.info(JSON.stringify(resp.data));
            // if(this.$store.StaStustudentsign){
            //   location.reload();
            //   this.$store.commit('updateStaStustudentsign');
            // }
          });

        });
      },
    },
    data() {
      return {
        studentInfo: {
          id: "",
          basicId: "",
          studentId: "",
          classId: "",
          sb: {},
          c: {},
        },
        dormitoryInfo: {
          id: "",
          dong: "",
          floor: "",
          room: "",
          sex: "",
          number: "",
          rent: ""
        },
        headmasterInfo: {},

        allLoading: false,


        //班级通知
        classnotice:[],
      }
    }
  }
</script>

<style scoped>
  /*.container {*/
  /*  padding: 30px;*/
  /*  background: #fff;*/
  /*  border: 1px solid #ddd;*/
  /*  border-radius: 5px;*/
  /*  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);*/
  /*}*/
  /*.divbuju {*/
  /*  margin: 30px;*/
  /*  background: #fff;*/
  /*  border: 2px solid #ddd;*/
  /*  border-radius: 5px;*/
  /*  //box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .12), 0 0 6px 0 rgba(0, 0, 0, .04);*/
  /*}*/
</style>



